import { Unity, useUnityContext } from "react-unity-webgl";
import { useState, useEffect, useCallback } from 'react'
import "../../css/games.css"
function Games(props) {
    const { name } = props.name;
    const [received, setReceived] = useState(0)
    const [canPlay, setCanPlay] = useState(false)
    const UnityConfig1 = useUnityContext({
        loaderUrl: "/Portfolio/" + props.name + "/Build/" + props.name + ".loader.js",
        dataUrl: "/Portfolio/" + props.name + "/Build/" + props.name + ".data",
        frameworkUrl: "/Portfolio/" + props.name + "/Build/" + props.name + ".framework.js",
        codeUrl: "/Portfolio/" + props.name + "/Build/" + props.name + ".wasm",
        streamingAssetsUrl: "/Portfolio/" + props.name + "/StreamingAssets",
    });
    /*  const { unityProvider, isLoaded, loadingProgression, unload, requestFullscreen  } = useUnityContext({
           loaderUrl: "/Portfolio/DreamWebgl/Build/DreamWebgl.loader.js",
       dataUrl: "/Portfolio/DreamWebgl/Build/DreamWebgl.data",
       frameworkUrl: "/Portfolio/DreamWebgl/Build/DreamWebgl.framework.js",
       codeUrl: "/Portfolio/DreamWebgl/Build/DreamWebgl.wasm",
   
       });
   */
    const { unityProvider, isLoaded, loadingProgression, unload, requestFullscreen } = useUnityContext({
        loaderUrl: "/Portfolio/PBGJ5/Build/PBGJ5.loader.js",
        dataUrl: "/Portfolio/PBGJ5/Build/PBGJ5.data",
        frameworkUrl: "/Portfolio/PBGJ5/Build/PBGJ5.framework.js",
        codeUrl: "/Portfolio/PBGJ5/Build/PBGJ5.wasm",
        streamingAssetsUrl: "/Portfolio/PBGJ5/StreamingAssets",
    });
    const loadingPercentage = Math.round(loadingProgression * 100);


    // We'll use a state to store the device pixel ratio.
    const [devicePixelRatio, setDevicePixelRatio] = useState(
        window.devicePixelRatio
    );

    const handleChangePixelRatio = useCallback(
        function () {
            // A function which will update the device pixel ratio of the Unity
            // Application to match the device pixel ratio of the browser.
            const updateDevicePixelRatio = function () {
                setDevicePixelRatio(window.devicePixelRatio);
            };
            // A media matcher which watches for changes in the device pixel ratio.
            const mediaMatcher = window.matchMedia(
                `screen and (resolution: ${devicePixelRatio}dppx)`
            );
            // Adding an event listener to the media matcher which will update the
            // device pixel ratio of the Unity Application when the device pixel
            // ratio changes.
            mediaMatcher.addEventListener("change", updateDevicePixelRatio);
            return function () {
                // Removing the event listener when the component unmounts.
                mediaMatcher.removeEventListener("change", updateDevicePixelRatio);
            };
        },
        [devicePixelRatio]
    );
    function handleClick() {
        //await UnityConfig1.unload();
        window.location.reload(true)
        props.setToogle(-1)
    }
    function handleClickEnterFullscreen() {
        UnityConfig1.requestFullscreen(true);
    }
    function Play() {
        setCanPlay(true);

    }
    return (
        <>

            <div className="game_screen-container1">
                {UnityConfig1.isLoaded}
                {canPlay && (
                    <>
                        {!UnityConfig1.isLoaded && (<h3>Loading Application... {Math.round(UnityConfig1.loadingProgression * 100)}%</h3>)}
                        <Unity style={{ width: 700, height: 400 }} unityProvider={UnityConfig1.unityProvider} devicePixelRatio={devicePixelRatio} />
                    </>
                )}
            </div>
            <div className="game_options-container1">
                <i onClick={Play} class="fa-solid fa-circle-play fa-3x"></i>
                {UnityConfig1.isLoaded && (
                    <>
                        <i onClick={handleClickEnterFullscreen} class="fa-solid fa-up-right-and-down-left-from-center fa-3x"></i>
                        <i onClick={handleClick} class="fa-solid fa-circle-stop fa-3x"></i>
                    </>
                )}
            </div>
            
            
        </>
    );
}

export default Games