import React, { useState } from 'react'
import "../css/portfolio.css"
import Menu from './Menu'
import Games from './webgl/Game';
import Details from './Details';
function Portfolio() {
    const [items, setItems] = useState(Menu);
    const [toogleState, setToogle] = useState(-1);
    const toogleTab = (index) => {
        setToogle(index);
    }
    const filterItem = (categoryItem) => {
        const updatedItems = Menu.filter((curElem) => {
            return curElem.category === categoryItem;
        })
        setItems(updatedItems);
    }

    return (
        <section className='work container section' id="work">
            <h2 className='section_title'>Recent Works</h2>
            <div className='work_filters'>
                <span className='work_item' onClick={() => setItems(Menu)}>Everything</span>
                <span className='work_item' onClick={() => filterItem("Game")}>Game</span>
                <span className='work_item' onClick={() => filterItem("Web")}>WebSite</span>
                <span className='work_item' onClick={() => filterItem("AI")}>AI</span>
            </div>
            <div className='work_container grid'>
                {items.map((item, index) => {
                    const { id, image, title, category, techno, link, pathName, video, gallery, description} = item;
                    return (
                        <div className='work_card' key={index}>
                            <div className='work_thumbnail'>
                                <img src={image} alt="" className='work_img' />
                                <div className='work_mask'></div>
                            </div>

                            <span className='work_category'>{category}</span>
                            <h3 className='work_title'>{title}</h3>
                            <h3 className='work_techno'>{techno}</h3>
                            {/* {category === "Game" && (
                               
                            )} */}
                             <span className='work_play' onClick={() => toogleTab(index)}><i class="fa-solid fa-circle-play fa-3x"></i></span>
                            {link != "" &&(<a href={link} className="work_button" target="_blank">
                                <i className="icon-link work_button-icon"></i>
                            </a>)}
                            

                            <div className={toogleState == index ? "work_modal active-modal" : "work_modal"}>
                                <div className="work_modal-content">

                                    <i className="icon-close work_modal-close" onClick={() => toogleTab(-1)}></i>


                                    <h3 className='work_modal-title'> {title}  </h3>
                                    <div className="work_modal-body">
                                        {pathName != null && (<Games name={pathName} desc={description}  setToogle={setToogle} />)}
                                        
                                        <Details desc={description} video={video} techno={techno} gallery={gallery}setToogle={setToogle} />

                                       
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                }
                )}
            </div>
        </section>
    )
}

export default Portfolio